import React from 'react';
import { makeStyles } from '@material-ui/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import NoSsr from '@material-ui/core/NoSsr';
import Tab from '@material-ui/core/Tab';
import { Link, Route, Switch } from 'react-router-dom';
import { STYLES, ROUTER_PREFIXES } from '../config';
import Chart from '../components/Chart';
import TasksTable from '../components/TasksTable';

const { CHART, TABLE } = ROUTER_PREFIXES;
const { TABS_BACKGROUND } = STYLES;

export default function NavTabs() {
  const { root } = useStyles();

  return (
    <Route
      render={({ match }) => {
        return (
          <NoSsr>
            <div className={root}>
              <AppBar position='static'>
                <Tabs
                  style={{ background: TABS_BACKGROUND }}
                  variant='fullWidth'
                  value={match.url.endsWith(CHART) ? 1 : 0}
                >
                  <Tab label={'TASKS LOG'} component={Link} to={TABLE} />
                  <Tab
                    label={'TASKS CHART'}
                    component={Link}
                    to={`/${CHART}`}
                    replace={true}
                  />
                </Tabs>
              </AppBar>

              <Switch>
                <Route path={`/${CHART}`} component={Chart} />

                <Route path={TABLE} component={TasksTable} />
              </Switch>
            </div>
          </NoSsr>
        );
      }}
    />
  );
}

function LinkTab(props) {
  return <Tab component={Link} {...props} />;
}

const useStyles = makeStyles(() => ({
  root: {
    flexGrow: 1
  }
}));
