import React, { PureComponent } from 'react';
import styled from 'styled-components';
import moment from 'moment';
import { getFormattedDifference } from '../utils/timeFormatter';

const DEFAULT_DISPLAY_TIME = '00:00:00';

export default class Clock extends PureComponent {
  clock = React.createRef();

  state = {
    timeToDisplay: DEFAULT_DISPLAY_TIME
  };

  launchTimer = () => {
    this.timer = setInterval(this.updateStateTime, 1000);
  };
  destroyTimer = () => {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
  };

  updateStateTime = () => {
    const { startTime } = this.props;

    const isValidValue = moment(startTime).isValid();

    /* we don't update if no ref - memory leak prevented  */
    if (this.clock.current) {
      this.setState({
        timeToDisplay: isValidValue
          ? getFormattedDifference(startTime)
          : DEFAULT_DISPLAY_TIME
      });
    }
  };

  componentDidMount() {
    this.launchTimer();
  }

  componentWillReceiveProps() {
    this.launchTimer();
  }

  componentWillUnmount() {
    this.destroyTimer();
  }

  render() {
    const { timeToDisplay } = this.state;

    return (
      <ClockRoundBlock>
        <TimeOutputWrapper ref={this.clock}>{timeToDisplay}</TimeOutputWrapper>
      </ClockRoundBlock>
    );
  }
}

const ClockRoundBlock = styled.div`
  height: 200px;
  width: 200px;
  border: 1px solid lightgray;
  margin: 30px;
  border-radius: 50%;
  box-shadow: 0 2px 3px 1px lightgrey;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const TimeOutputWrapper = styled.span`
  font-size: 36px;
`;
