import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import _ from 'lodash';
import styled from 'styled-components';
import { Input, withStyles } from '@material-ui/core';
import Clock from './Clock';
import BasicButton from './BasicButton';
import { STYLES } from '../config';
import EmptyTaskAlertDialog from './EmptyTaskAlertDialog';
import { controlTasksFlow, setTaskName } from '../store/actions';

const { TEXT_COLOR } = STYLES;

class Timer extends Component {
  state = {
    initialized: false,
    showEmptyTaskAlert: false,
    localTaskName: ''
  };
  openAlert = () => {
    this.setState({ showEmptyTaskAlert: true });
  };
  closeAlert = () => {
    this.setState({ showEmptyTaskAlert: false });
  };

  handleTimerButtonClick = () => {
    const { startTime, taskName, controlTasksFlow } = this.props;

    if (startTime && !taskName) {
      /* empty task name message */
      this.openAlert();
    } else if (startTime && taskName) {
      /* task should be created successfully, so we have to nullify task name */
      this.setState({ localTaskName: '' });
      controlTasksFlow();
    } else {
      /* for rest cases we run task flow */
      controlTasksFlow();
    }
  };

  handleInputChange = e => {
    const value = e.target.value;

    this.setState({ localTaskName: value }, () => {
      this.debouncedTaskNameSetter(value);
    });
  };

  debouncedTaskNameSetter = _.debounce(this.props.setTaskName, 150);

  componentWillReceiveProps(nextProps) {
    const { taskName } = nextProps;
    const { initialized } = this.state;

    if (!initialized) {
      this.setState({
        localTaskName: taskName,
        initialized: true
      });
    }
  }

  render() {
    const {
      props,
      state,
      handleTimerButtonClick,
      handleInputChange,
      closeAlert
    } = this;
    const { startTime } = props;
    const { localTaskName, showEmptyTaskAlert } = state;

    return (
      <TimerContainer>
        <TaskNameInput
          onChange={handleInputChange}
          placeholder={'Name of your task'}
          value={localTaskName}
          inputProps={{
            style: {
              textAlign: 'center'
            }
          }}
        />

        <EmptyTaskAlertDialog
          open={showEmptyTaskAlert}
          closeAlert={closeAlert}
        />

        <Clock startTime={startTime} />

        <BasicButton onClick={handleTimerButtonClick}>
          {startTime ? 'STOP' : 'START'}
        </BasicButton>
      </TimerContainer>
    );
  }
}

export default connect(
  ({ currentTask }) => ({
    startTime: currentTask.startTime,
    taskName: currentTask.taskName
  }),
  dispatch => bindActionCreators({ controlTasksFlow, setTaskName }, dispatch)
)(Timer);

const TimerContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-between;
  align-items: center;
  color: ${TEXT_COLOR};
  padding-top: 30px;
`;

const TaskNameInput = withStyles({
  root: {
    width: '450px',
    fontSize: '20px',
    color: `${TEXT_COLOR}`
  }
})(Input);
